            }
        }
    }

    #logo{
        width: 130px;
        top:2em;
    }

    .main-menu{
        // background-color: red;
        font-size: 11px;
    }

    .box{
        h2{
            font-size: 25px ;
            margin-top: 10px;
        }

        // background-color: red;
        width: 500px;
        height: 215px;
        right: 7em;
        z-index: 99999;
        padding: 10px 0 20px 0;


        .btn{
            font-size: 15px;
        }
