}

@media only screen and (max-width : 767px) {

    #main-content-container{
        // background-color: blue;
    }

    #logo{
        width: 90px;
        top:10em;
    }

    .main-menu{
        // background-color: blue;
        &-links{
            width: 75%;
            float: right;
            margin-right: 30px;
            margin-bottom: 30px;
        }
    }

    .box{
        display: flex;
        position:absolute;
        // background-color: blue;
        width: 100%;
        height: 75px;
        right: 0;
        padding-right: 20px;
        top:inherit;
        h2{
            font-size: 13px;
            &:first-of-type{
                margin-left: 10px;
            }
            &:last-of-type{
                margin-right: 10px;
            }
        }

        .btn{
            font-size: 10px;
            margin-top: 1em;
        }
    }

}

